.App {
     font-family: sans-serif;
     text-align: center;
}

.inputBox {
     height: 14rem;
     width: 100%;
}
textarea {
     padding: 1rem;
     border: none;
     overflow: auto;
     outline: none;

     -webkit-box-shadow: none;
     -moz-box-shadow: none;
     box-shadow: none;

     resize: none; /*remove the resize handle on the bottom right*/
}
