@charset "UTF-8";
body {
     background: #f4f6fc;
}

.iphone-x {
     position: relative;
     margin: 40px auto;
     width: 260px;
     height: 580px;
     border-radius: 40px;
     border: 10px solid #212121;
     /* box-shadow: 0px 0px 0px 11px #1f1f1f, 0px 0px 0px 13px #191919,
          0px 0px 0px 20px #111; */
}
.iphone-x:before,
.iphone-x:after {
     content: "";
     position: absolute;
     left: 50%;
     transform: translateX(-50%);
}
.iphone-x:after {
     bottom: 7px;
     width: 140px;
     height: 4px;
     background-color: #f2f2f2;
     border-radius: 10px;
}

.iphone-x i,
.iphone-x b,
.iphone-x s,
.iphone-x span {
     position: absolute;
     display: block;
     color: transparent;
}

.iphone-x s {
     top: 50px;
     color: #212121;
     text-align: center;
     text-decoration: none;
     width: 100%;
     font-size: 20px;
     font-weight: 100;
     padding-top: 60px;
}
.iphone-x span {
     bottom: 50px;
     width: 40px;
     height: 40px;
     background-color: rgba(0, 0, 0, 0.3);
     border-radius: 50%;
     left: 30px;
}
.iphone-x span + span {
     left: auto;
     right: 30px;
}
